import React, { useState } from "react";

const supportFaqs = [
  {
    id: 1,
    question: "What file formats does View4Sight support?",
    answer: "View4Sight supports all major 3D point cloud formats including LAS, LAZ, E57, PLY, XYZ, and PTS files. We also support IFC and DXF formats for CAD integration, plus orthophotos in common image formats."
  },
  {
    id: 2,
    question: "How do I upload large files to View4Sight?",
    answer: "For files larger than 2GB, we recommend using our chunked upload feature. You can also compress your files or contact support for assistance with very large datasets. Enterprise accounts have higher upload limits."
  },
  {
    id: 3,
    question: "Why is my point cloud not displaying correctly?",
    answer: "Common causes include: unsupported coordinate system, corrupted file, or browser compatibility issues. Try refreshing the page, checking your internet connection, or converting your file to a supported format."
  },
  {
    id: 4,
    question: "How can I share my 3D data with clients?",
    answer: "You can create secure sharing links with password protection and access controls. Set viewing permissions, expiration dates, and track who has accessed your data through the sharing dashboard."
  },
  {
    id: 5,
    question: "What are the system requirements?",
    answer: "View4Sight works on any modern browser (Chrome, Firefox, Safari, Edge). We recommend 8GB RAM for optimal performance. No special software installation required - it's completely web-based."
  },
  {
    id: 6,
    question: "How do I integrate View4Sight with my existing workflow?",
    answer: "Use our REST API for custom integrations, or check our available plugins for popular surveying software. Our team can also help design custom integration solutions for enterprise clients."
  }
];

export default function SupportFaq() {
  const [openFaq, setOpenFaq] = useState(null);

  const toggleFaq = (id) => {
    setOpenFaq(openFaq === id ? null : id);
  };

  return (
    <div id="support-faq" className="section panel overflow-hidden">
      <div className="section-outer panel py-6 sm:py-8 lg:py-9">
        <div className="container max-w-lg">
          <div className="section-inner panel">
            <div className="panel vstack gap-4 sm:gap-6 xl:gap-8">
              <div className="panel vstack items-center gap-2 xl:gap-3 text-center">
                <h2
                  className="h4 sm:h3 lg:h2 m-0"
                  data-anime="onview: -100; translateY: [48, 0]; opacity: [0, 1]; easing: spring(1, 80, 10, 0); duration: 450; delay: 100;"
                >
                  Frequently Asked Questions
                </h2>
                <p className="fs-6 xl:fs-5 text-dark dark:text-white text-opacity-70 max-w-lg mx-auto">
                  Quick answers to common technical questions
                </p>
              </div>
              
              <div className="panel">
                <div className="vstack gap-2">
                  {supportFaqs.map((faq) => (
                    <div
                      key={faq.id}
                      className="panel border border-gray-100 dark:border-gray-800 rounded-2 overflow-hidden"
                    >
                      <button
                        className="btn w-100 text-start p-4 d-flex justify-content-between align-items-center"
                        onClick={() => toggleFaq(faq.id)}
                      >
                        <span className="fw-medium text-dark dark:text-white">
                          {faq.question}
                        </span>
                        <i className={`unicon-chevron-${openFaq === faq.id ? 'up' : 'down'} text-primary`}></i>
                      </button>
                      {openFaq === faq.id && (
                        <div className="panel px-4 pb-4">
                          <p className="text-dark dark:text-white text-opacity-70 m-0">
                            {faq.answer}
                          </p>
                        </div>
                      )}
                    </div>
                  ))}
                </div>
              </div>
              
              <div className="panel text-center">
                <p className="text-dark dark:text-white text-opacity-70 mb-3">
                  Still need help? Our support team is here for you.
                </p>
                <a
                  href="mailto:support@view4sight.com"
                  className="btn btn-primary btn-md text-white"
                >
                  Contact Support
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
} 